define('obey/utils/htmlEscape', ['exports', 'module'], function (exports, module) {
  module.exports = escapeHtml;
  var entityMap = {
    '&': '&amp;',
    '<': '&lt;',
    '>': '&gt;',
    '"': '&quot;',
    '\'': '&#39;',
    '/': '&#x2F;'
  };

  function escapeHtml(string) {
    return String(string).replace(/[&<>"'\/]/g, function (str) {
      return entityMap[str];
    });
  }
});